.iti--allow-dropdown
	.iti__country-container:not(:has(+ input[disabled])):not(
		:has(+ input[readonly])
	):hover,
.iti--allow-dropdown
	.iti__country-container:not(:has(+ input[disabled])):not(
		:has(+ input[readonly])
	):hover
	button,
.iti--container:hover {
	cursor: pointer;
}
:root {
	--iti-hover-color: #e5e7eb;
	--iti-border-color: #ccc;
	--iti-dialcode-color: #999;
	--iti-dropdown-bg: white;
	--iti-spacer-horizontal: 8px;
	--iti-flag-height: 12px;
	--iti-flag-width: 16px;
	--iti-border-width: 1px;
	--iti-arrow-height: 4px;
	--iti-arrow-width: 6px;
	--iti-triangle-border: calc(var(--iti-arrow-width) / 2);
	--iti-arrow-padding: 6px;
	--iti-arrow-color: #555;
	--iti-path-flags-1x: url("../../images/flags/flags.webp");
	--iti-path-flags-2x: url("../../images/flags/flags-2x.webp");
	--iti-path-globe-1x: url("../../images/flags/globe.webp");
	--iti-path-globe-2x: url("../../images/flags/globe-2x.webp");
	--iti-flag-sprite-width: 3904px;
	--iti-flag-sprite-height: 12px;
	--iti-mobile-popup-margin: 30px;
}
.iti {
	position: relative;
	display: inline-block;
}
.iti * {
	box-sizing: border-box;
}
.iti__hide {
	display: none;
}
.iti__v-hide {
	visibility: hidden;
}
.iti__a11y-text {
	width: 1px;
	height: 1px;
	clip: rect(1px, 1px, 1px, 1px);
	overflow: hidden;
	position: absolute;
}
.iti input.iti__tel-input,
.iti input.iti__tel-input[type="tel"],
.iti input.iti__tel-input[type="text"] {
	position: relative;
	z-index: 0;
	margin: 0 !important;
}
.iti__country-container {
	position: absolute;
	top: 0;
	bottom: 0;
	padding: var(--iti-border-width);
}
.iti__selected-country {
	z-index: 1;
	position: relative;
	display: flex;
	align-items: center;
	height: 100%;
	background: 0 0;
	border: 0;
	margin: 0;
	padding: 0;
	font-family: inherit;
	font-size: inherit;
	color: inherit;
	border-radius: 0;
	font-weight: inherit;
	line-height: inherit;
	text-decoration: none;
}
.iti__selected-country-primary {
	display: flex;
	align-items: center;
	height: 100%;
	padding: 0 var(--iti-arrow-padding) 0 var(--iti-spacer-horizontal);
}
.iti__arrow {
	margin-left: var(--iti-arrow-padding);
	width: 0;
	height: 0;
	border-left: var(--iti-triangle-border) solid transparent;
	border-right: var(--iti-triangle-border) solid transparent;
	border-top: var(--iti-arrow-height) solid var(--iti-arrow-color);
}
[dir="rtl"] .iti__arrow {
	margin-right: var(--iti-arrow-padding);
	margin-left: 0;
}
.iti__arrow--up {
	border-top: none;
	border-bottom: var(--iti-arrow-height) solid var(--iti-arrow-color);
}
.iti__dropdown-content {
	border-radius: 3px;
	background-color: var(--iti-dropdown-bg);
}
.iti--inline-dropdown .iti__dropdown-content {
	position: absolute;
	z-index: 2;
	margin-top: 3px;
	margin-left: calc(var(--iti-border-width) * -1);
	border: var(--iti-border-width) solid var(--iti-border-color);
	box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
}
.iti__search-input {
	width: 100%;
	border-width: 0;
	border-radius: 3px;
}
.iti__search-input + .iti__country-list {
	border-top: 1px solid var(--iti-border-color);
}
.iti__country-list {
	list-style: none;
	padding: 0;
	margin: 0;
	overflow-y: scroll;
	-webkit-overflow-scrolling: touch;
}
.iti--inline-dropdown .iti__country-list {
	max-height: 185px;
}
.iti--flexible-dropdown-width .iti__country-list {
	white-space: nowrap;
}
@media (max-width: 500px) {
	.iti--flexible-dropdown-width .iti__country-list {
		white-space: normal;
	}
}
.iti__country {
	display: flex;
	align-items: center;
	padding: 8px var(--iti-spacer-horizontal);
	outline: 0;
}
.iti__dial-code {
	color: var(--iti-dialcode-color);
}
.iti__country.iti__highlight {
	background-color: var(--iti-hover-color);
	border-radius: 8px;
}
.iti__country-list .iti__flag,
.iti__country-name {
	margin-right: var(--iti-spacer-horizontal);
}
[dir="rtl"] .iti__country-list .iti__flag,
[dir="rtl"] .iti__country-name {
	margin-right: 0;
	margin-left: var(--iti-spacer-horizontal);
}
.iti--allow-dropdown
	.iti__country-container:not(:has(+ input[disabled])):not(
		:has(+ input[readonly])
	)
	.iti__selected-country-primary:hover,
.iti--allow-dropdown
	.iti__country-container:not(:has(+ input[disabled])):not(
		:has(+ input[readonly])
	)
	.iti__selected-country:has(+ .iti__dropdown-content:hover)
	.iti__selected-country-primary {
	background-color: var(--iti-hover-color);
}
.iti .iti__selected-dial-code {
	margin-left: 4px;
}
[dir="rtl"] .iti .iti__selected-dial-code {
	margin-left: 0;
	margin-right: 4px;
}
.iti--container {
	position: fixed;
	top: -1000px;
	left: -1000px;
	z-index: 1060;
	padding: var(--iti-border-width);
}
.iti--fullscreen-popup.iti--container {
	background-color: rgba(0, 0, 0, 0.5);
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	position: fixed;
	padding: var(--iti-mobile-popup-margin);
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
}
.iti--fullscreen-popup .iti__dropdown-content {
	display: flex;
	flex-direction: column;
	max-height: 100%;
	position: relative;
}
.iti--fullscreen-popup .iti__country {
	padding: 10px;
	line-height: 1.5em;
}
